import React,{Component} from 'react'
import { withRouter } from 'react-router-dom';
import fotodefecto from '../imagenes/userDefect.png'
import auth from './auth'
import Chart from "react-apexcharts"
import '../css/estadisticas.css'
import ipserver from '../config/config'
import Header from './header'


var horariosX = [ '','']
var horariosY = [1,2]
var titulo = ''

class EstadisticasPage extends Component{

    constructor(props){
        super(props)
        this.state = {
            data:null,
            nick:'none',
            users:[],
            chars:[],
            dia:0,
            selectValue:0,
            plot1:true,
            plot2:false,
            plot3:false,
            plot4:false,
            plot5:false,
            plot6:false,
            xplot1:[],
            yplot1:[],
            xhorasLunesplot2:[],
            yhorasLunesplot2:[],
            xhorasMartesplot2:[],
            yhorasMartesplot2:[],
            xhorasMiercolesplot2:[],
            yhorasMiercolesplot2:[],
            xhorasJuevesplot2:[],
            yhorasJuevesplot2:[],
            xhorasViernesplot2:[],
            yhorasViernesplot2:[],
            xhorasSabadoplot2:[],
            yhorasSabadoplot2:[],
            xhorasDomingoplot2:[],
            yhorasDomingoplot2:[],
            clasesPlot3:[],
            frecClasesPlot3:[],
            xPlot5:[],
            yPlot5:[]  
        }
        /**Referencias */
        this.imagen = React.createRef()
        this.opciones = React.createRef()
    }

    async componentDidMount() {
        window.scrollTo(0, 0)
        const {data} = this.props.location.state   
        var foto = (data.data.foto).toString()
        if(  foto === 'defecto' || !foto ){
            this.imagen.current.src = fotodefecto
        }else {
            this.imagen.current.src = data.data.foto
        }
        //--------- Traigo todos los usuarios ----------------
        const url = ipserver+'api/user/'
        const response = await fetch(url,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta = await response.json()

        const url2 = ipserver+'api/char/'
        const response2 = await fetch(url2,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta2 = await response2.json()

        if( (respuesta.status === 0) && (respuesta2.status === 0) ){
            this.setState({
                data:data,
                nick:data.data.nickname,
                users:respuesta.data,
                chars:respuesta2.data
            })
        }

        this.analizarDatos()
    }

    logout(){
        auth.signout()
        this.props.history.push({  pathname: '/' })
    }

    analizarDatos(){
        //-------- Jugadores al dia --------

         var dic = {
            'lunes':0,
            'martes':0,
            'miercoles':0,
            'jueves':0,
            'viernes':0,
            'sabado':0,
            'domingo':0
        }
        this.state.users.map( (e,i)=>{
            if(e.L) dic['lunes'] = dic['lunes'] + 1
            if(e.M) dic['martes'] = dic['martes'] + 1
            if(e.MI) dic['miercoles'] = dic['miercoles'] + 1
            if(e.J) dic['jueves'] = dic['jueves'] + 1
            if(e.V) dic['viernes'] = dic['viernes'] + 1
            if(e.S) dic['sabado'] = dic['sabado'] + 1
            if(e.D) dic['domingo'] = dic['domingo'] + 1
            return 0
        } )
        var dias=[]
        var frec = []
        for (const [key, value] of Object.entries(dic)) {
            dias.push(key)
            frec.push(value)
        }
        this.setState({
            xplot1:dias,
            yplot1:frec
        })
        //-------- Distribucion de horas --------

        var xHorasLunes = []
        var yHorasLunes = []
        var xHorasMartes = []
        var yHorasMartes = []
        var xHorasMiercoles = []
        var yHorasMiercoles = []
        var xHorasJueves = []
        var yHorasJueves = []
        var xHorasViernes = []
        var yHorasViernes = []
        var xHorasSabado = []
        var yHorasSabado = []
        var xHorasDomingo = []
        var yHorasDomingo = []
        this.state.users.map( (e,i)=>{
            if(e.HL !== '') {    
                xHorasLunes.push( e.nickname)
                yHorasLunes.push( parseInt(e.HL.split(':')[0], 10) )
            }
            if(e.HM !== '') {    
                xHorasMartes.push( e.nickname)
                yHorasMartes.push( parseInt(e.HM.split(':')[0], 10) )
            }
            if(e.HMI !== '') {    
                xHorasMiercoles.push( e.nickname)
                yHorasMiercoles.push( parseInt(e.HMI.split(':')[0], 10) )
            }
            if(e.HJ !== '') {    
                xHorasJueves.push( e.nickname)
                yHorasJueves.push( parseInt(e.HJ.split(':')[0], 10) )
            }
            if(e.HV !== '') {    
                xHorasViernes.push( e.nickname)
                yHorasViernes.push( parseInt(e.HV.split(':')[0], 10) )
            }
            if(e.HS !== '') {    
                xHorasSabado.push( e.nickname)
                yHorasSabado.push( parseInt(e.HS.split(':')[0], 10) )
            }
            if(e.HD !== '') {    
                xHorasDomingo.push( e.nickname)
                yHorasDomingo.push( parseInt(e.HD.split(':')[0], 10) )
            }
            return 0
        })
        this.setState({
            xhorasLunesplot2:xHorasLunes,
            yhorasLunesplot2:yHorasLunes,
            xhorasMartesplot2:xHorasMartes,
            yhorasMartesplot2:yHorasMartes,
            xhorasMiercolesplot2:xHorasMiercoles,
            yhorasMiercolesplot2:yHorasMiercoles,
            xhorasJuevesplot2:xHorasJueves,
            yhorasJuevesplot2:yHorasJueves,
            xhorasViernesplot2:xHorasViernes,
            yhorasViernesplot2:yHorasViernes,
            xhorasSabadoplot2:xHorasSabado,
            yhorasSabadoplot2:yHorasSabado,
            xhorasDomingoplot2:xHorasDomingo,
            yhorasDomingoplot2:yHorasDomingo
        })
        //------------Personajes ------------------

        var dic2 = {
            'Sorccer':0,
            'Archer':0,
            'Gunner':0,
            'Reaper':0,
            'Slayer':0,
            'Warrior':0,
            'Lancer':0,
            'Priest':0,
            'Mystic':0,
            'Berserker':0
        }
        this.state.chars.map( (e,i)=>{
            if(e.clase === 'Sorccer') dic2['Sorccer'] = dic2['Sorccer'] + 1
            if(e.clase === 'Archer') dic2['Archer'] = dic2['Archer'] + 1
            if(e.clase === 'Gunner') dic2['Gunner'] = dic2['Gunner'] + 1
            if(e.clase === 'Reaper') dic2['Reaper'] = dic2['Reaper'] + 1
            if(e.clase === 'Slayer') dic2['Slayer'] = dic2['Slayer'] + 1
            if(e.clase === 'Warrior') dic2['Warrior'] = dic2['Warrior'] + 1
            if(e.clase === 'Lancer') dic2['Lancer'] = dic2['Lancer'] + 1
            if(e.clase === 'Priest') dic2['Priest'] = dic2['Priest'] + 1
            if(e.clase === 'Mystic') dic2['Mystic'] = dic2['Mystic'] + 1
            if(e.clase === 'Berserker') dic2['Berserker'] = dic2['Berserker'] + 1
            return 0
        } )
        var clases=[]
        var frecClases = []
        for (const [key, value] of Object.entries(dic2)) {
            clases.push(key)
            frecClases.push(value)
        }
        this.setState({
            clasesPlot3:clases,
            frecClasesPlot3:frecClases
        })
        //----------- Alianza -----------------

        var xAlianza = []
        var yAlianza = []
        var alianzaDic = {
            'Si':0,
            'No':0
        }
        this.state.chars.map( (e,i)=>{
            if(e.rankeado) alianzaDic['Si'] = alianzaDic['Si'] + 1
            else alianzaDic['No'] = alianzaDic['No'] + 1
            return 0
        } )
        for (const [key, value] of Object.entries(alianzaDic)) {
            xAlianza.push(key)
            yAlianza.push(value)
        }
        this.setState({
            xPlot5:xAlianza,
            yPlot5:yAlianza
        })
    }

    nextPageInicio(){
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/inicio',
            state: { data:this.state.data}
        })}
    }

    nextPageMiembros(){
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/miembros',
            state: {  data: this.state.data }
        })}
    }

    nextPageGuias(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/guias',
                state: {  data: this.state.data }
            })
        }
    }

    nextPageEditarperfil(){
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/editar',
            state: {  data: this.state.data }
        })}
    }

    plot1(){
        this.setState({
            plot1:true,
            plot2:false,
            plot3:false,
            plot4:false,
            plot5:false,
            plot6:false
        })
    }

    plot2(){
        this.setState({
            plot1:false,
            plot2:true,
            plot3:false,
            plot4:false,
            plot5:false,
            plot6:false
        })
    }

    plot3(){
        this.setState({
            plot1:false,
            plot2:false,
            plot3:true,
            plot4:false,
            plot5:false,
            plot6:false
        })
    }
    
    plot4(){
        this.setState({
            plot1:false,
            plot2:false,
            plot3:false,
            plot4:true,
            plot5:false,
            plot6:false
        })
    }

    plot5(){
        this.setState({
            plot1:false,
            plot2:false,
            plot3:false,
            plot4:false,
            plot5:true,
            plot6:false
        })
    }

    cambiarDia(dia){
        this.setState({ dia:dia })
    }

    opcion3(){  //Quien necesita algo starfall     
        var lista = []
        var ok = false
        var mensaje = ''
        this.state.chars.map( (e,i)=>{  
            ok=false
            if((e.arma !== 'Starfall')  ){ mensaje = 'Arma';ok= true }
            if((e.armadura !== 'Starfall')  ){ mensaje += ', Armadura';ok= true }
            if((e.botas !== 'Starfall')  ){ mensaje += ', Botas';ok= true }
            if((e.guantes !== 'Starfall') ){ mensaje += ', Guantes';ok= true }
            if((e.cinturon !== 'Starfall') ){ mensaje += ', Cinturon';ok= true }
            if(ok){ lista.push(e.nombre+'-> Necesita : '+mensaje) }
            return 0
        } )
        return(
            <div className='App-div-29'>  
                { lista.map((e,i)=>{ return( <p key={i} className='App-texto-10' >{e}</p> ) })}
            </div>
        )
    }

    opcion2(){  //Quien necesita algo deviate     
        var lista = []
        var ok = false
        var mensaje = ''
        this.state.chars.map( (e,i)=>{  
            ok=false
            if((e.arma !== 'Deviate') && (e.arma !== 'Tensus') && (e.arma !== 'Starfall') ){ mensaje = 'Arma';ok= true }
            if((e.armadura !== 'Deviate') && (e.armadura !== 'Tensus') && (e.arma !== 'Starfall') ){ mensaje += ', Armadura';ok= true }
            if((e.botas !== 'Deviate') && (e.botas !== 'Tensus') && (e.arma !== 'Starfall') ){ mensaje += ', Botas';ok= true }
            if((e.guantes !== 'Deviate') && (e.guantes !== 'Tensus') && (e.arma !== 'Starfall') ){ mensaje += ', Guantes';ok= true }
            if((e.cinturon !== 'Deviate') && (e.cinturon !== 'Tensus') && (e.arma !== 'Starfall') ){ mensaje += ', Cinturon';ok= true }
            if(ok){ lista.push(e.nombre+'-> Necesita : '+mensaje) }
            return 0
        } )
        return(
            <div className='App-div-29'>  
                { lista.map((e,i)=>{ return( <p key={i} className='App-texto-10'>{e}</p> ) })}
            </div>
        )
    }

    opcion1(){  //Quien necesita algo Tensus     
        var lista = []
        var ok = false
        var mensaje = ''
        this.state.chars.map( (e,i)=>{  
            ok=false
            if((e.arma !== 'Tensus') && (e.arma !== 'Starfall') ){ mensaje = 'Arma';ok= true }
            if((e.armadura !== 'Tensus') && (e.armadura !== 'Starfall') ){ mensaje += ', Armadura';ok= true }
            if((e.botas !== 'Tensus') && (e.botas !== 'Starfall') ){ mensaje += ', Botas';ok= true }
            if((e.guantes !== 'Tensus') && (e.guantes !== 'Starfall') ){ mensaje += ', Guantes';ok= true }
            if((e.cinturon !== 'Tensus') && (e.cinturon !== 'Starfall') ){ mensaje += ', Cinturon';ok= true }
            if(ok){ lista.push(e.nombre+'-> Necesita : '+mensaje) }
            return 0
        } )
        return(
            <div className='App-div-29'>  
                { lista.map((e,i)=>{ return( <p key={i} className='App-texto-10' >{e}</p> ) })}
            </div>
        )
    }

    opcion4(){  //Quien no esta rankeado     
        var lista = []
        this.state.chars.map( (e,i)=>{  
            if(!e.rankeado){ lista.push(e.nombre) }
            return 0
        } )
        return(
            <div className='App-div-29'>  
                { lista.map((e,i)=>{ return( <p key={i} className='App-texto-10' >{e}</p> ) })}
            </div>
        )
    }

    handleChange(event) {
        this.setState({selectValue: event.target.value});
    }

    render(){

        return(

            
                <div > 
                    <Header/>

                    <div className='estadisticas-div-1'>
                       

                        {/* Barra de navegacion  */}
                        <nav className='estadisticas-nav-1'>     
                            
                            <label title="Pagina principal" className="nav-link miembros-texto-1" onClick={this.nextPageInicio.bind(this)} >Inicio</label>
                            <label title="Lista de Miembros"  className="nav-link miembros-texto-1" onClick={this.nextPageMiembros.bind(this)} >Miembros</label>
                            <label title="Graficos"  className="nav-link miembros-texto-1" >Estadisticas</label>
                            <label title="Guias PvE PvP"  className="nav-link miembros-texto-1" onClick={this.nextPageGuias.bind(this)} >Guias</label>
                            <label title="Editar Perfil"  className="nav-link miembros-texto-1" onClick={this.nextPageEditarperfil.bind(this)}>Editar Perfil</label>             
                           
                        </nav>

                        {/* Perfil */}
                        <div className='estadisticas-div-perfil'> 
                            <center >                                                     
                                <img className='estadisticas-image-1'  ref={this.imagen}  alt='150x150'/>                                                                          
                                <center>
                                    <p className='estadisticas-texto-2'>{this.state.nick}</p> 
                                </center>                                                                                                          
                                <button className="btn4 btn-danger fa fa-sign-out" onClick={this.logout.bind(this)} title="Logout">logout</button>  
                            </center> 
                        </div>     
                        
                        {/**Estadisticas */}
                        <div className='estadisticas-div-2 border'>
                            <div className='estadisticas-div-3 border'>
                                <ul className="nav navbar-nav">
                                    <li className="nav-item"><label className="btn btn-info estadisticas-label-1 " onClick={()=>{this.plot1()}} >Jugadores al dia</label></li>
                                    <li className="nav-item"><label className="btn btn-info estadisticas-label-1" onClick={()=>{this.plot2()}} >Distribucion Horaria</label></li>
                                    <li className="nav-item"><label className="btn btn-info estadisticas-label-1" onClick={()=>{this.plot3()}}>Clases</label></li>
                                    <li className="nav-item"><label className="btn btn-info estadisticas-label-1" onClick={ ()=>{this.plot4()}}>Consultas varias</label></li>
                                    <li className="nav-item"><label className="btn btn-info estadisticas-label-1" onClick={ ()=>{this.plot5()}}>Alianza</label></li>            
                                </ul>                                                                    
                            </div>
                            <div className="estadisticas-div-4 ">
                                
                                <div className="mixed-chart">
                                        <div>
                                        { this.state.plot1 &&         
                                            <div>  
                                                 <center>
                                                 <label className='estadisticas-texto-3'>Jugadores al Dia</label>    
                                                
                                                <Chart
                                                    options={
                                                        {
                                                            chart: { id: "basic-bar"},
                                                            xaxis: { categories: this.state.xplot1},
                                                            fill: {
                                                                type: 'gradient',
                                                                gradient: {
                                                                shade: 'dark',
                                                                type: "horizontal",
                                                                shadeIntensity: 0.5,
                                                                gradientToColors: undefined, // optional, if not defined - uses the shades of same color in series
                                                                inverseColors: true,
                                                                opacityFrom: 1,
                                                                opacityTo: 1,
                                                                stops: [0, 50, 100],
                                                                colorStops: []
                                                                }
                                                            },
                                                        }
                                                    }
                                                    series={ [{ name: "Jugadores", data: this.state.yplot1  } ] }
                                                    type= 'bar'
                                                    width="500"  
                                                />
                                                </center> 
                                            </div>
                                            }
                                            { this.state.plot2 && 
                                                <div >
                                                    {(() =>{
                                                        switch(this.state.dia) {
                                                        case 0:
                                                            horariosX= this.state.xhorasLunesplot2
                                                            horariosY=this.state.yhorasLunesplot2
                                                            titulo='Lunes'
                                                            break
                                                        case 1:
                                                            horariosX= this.state.xhorasMartesplot2
                                                            horariosY=this.state.yhorasMartesplot2
                                                            titulo='Martes'
                                                            break
                                                        case 2:
                                                            horariosX= this.state.xhorasMiercolesplot2
                                                            horariosY=this.state.yhorasMiercolesplot2
                                                            titulo='Miercoles'
                                                            break
                                                        case 3:
                                                            horariosX= this.state.xhorasJuevesplot2
                                                            horariosY=this.state.yhorasJuevesplot2
                                                            titulo='Jueves'
                                                            break
                                                        case 4:
                                                            horariosX= this.state.xhorasViernesplot2
                                                            horariosY=this.state.yhorasViernesplot2
                                                            titulo='Viernes'
                                                            break
                                                        case 5:
                                                            horariosX= this.state.xhorasSabadoplot2
                                                            horariosY=this.state.yhorasSabadoplot2
                                                            titulo='Sabado'
                                                            break
                                                        case 6:
                                                            horariosX= this.state.xhorasDomingoplot2
                                                            horariosY=this.state.yhorasDomingoplot2
                                                            titulo='Domingo'
                                                            break
                                                        default:
                                                            return null;
                                                        }
                                                    })()}
                                                    <center>
                                                        <label className='estadisticas-texto-3'>{titulo}</label>   
                                                    </center> 
                                                    <div className='estadisticas-div-5'>
                                                    <Chart
                                                        options={
                                                            {
                                                                chart: { id: "basic-bar",type: 'scatter'},
                                                                grid: {
                                                                    padding: {
                                                                    left: 30,
                                                                    right: 60
                                                                    }
                                                                },
                                                                dataLabels: {
                                                                    enabled: true,
                                                                    enabledOnSeries: undefined,
                                                                    formatter: function (val, opts) {
                                                                        return val
                                                                    },
                                                                    textAnchor: 'middle',
                                                                    offsetX: 0,
                                                                    offsetY: 0,
                                                                    style: {
                                                                        fontSize: '24px',
                                                                        fontFamily: 'Helvetica, Arial, sans-serif',
                                                                        colors: undefined
                                                                    },
                                                                    dropShadow: {
                                                                        enabled: true,
                                                                        top: 1,
                                                                        left: 1,
                                                                        blur: 1,
                                                                        opacity: 0.45
                                                                    }
                                                                },
                                                                xaxis: { categories: horariosX }
                                                            }
                                                        }
                                                        series={ [{ name: "Hora GMT-3", data: horariosY  } ] }
                                                        type= 'scatter'
                                                        width="500"  
                                                    />  
                                                    <div className='estadisticas-div-6'>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(0)}}>Lunes</button>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(1)}}>Martes</button>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(2)}}>Miercoles</button>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(3)}}>Jueves</button>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(4)}}>Viernes</button>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(5)}}>Sabado</button>
                                                        <button className='btn btn-info estadisticas-div-7' onClick={()=>{this.cambiarDia(6)}}>Doming</button>
                                                    </div>
                                                    </div>
                                                </div>         
                                            }
                                            { this.state.plot3 && 
                                                <div>
                                                    <center>
                                                    <label className='estadisticas-texto-3'>Clases</label>   
                                                   
                                                    <Chart options={
                                                        {
                                                            chart: {
                                                            width: 380,
                                                            type: 'donut',
                                                            },
                                                            labels: this.state.clasesPlot3,
                                                            responsive: [{
                                                            breakpoint: 480,
                                                            options: {
                                                                chart: {
                                                                width: 200
                                                                },
                                                                legend: {
                                                                position: 'bottom'
                                                                }
                                                            }
                                                            }]
                                                        }
                                                    } series={ this.state.frecClasesPlot3  } type="donut" width={380} />     
                                                     </center>                               
                                                </div>
                                            }
                                            { this.state.plot4 && 
                                                <div className='estadisticas-div-8 border'>
                                                    <label className='estadisticas-texto-3'>Consultas Varias</label>  
                                                    <select value={this.state.value} onChange={this.handleChange.bind(this)}>
                                                        <option value="00">Elige una opcion</option>
                                                        <option value="02">¿Quien necesita sacar la Deviate?</option>
                                                        <option value="01">¿Quien necesita sacar la Tensus?</option>
                                                        <option value="03">¿Quien necesita sacar la Starfall?</option>
                                                        <option value="04">¿Quien no esta rankeado en Alianza?</option>  
                                                    </select><br/>
                                                    { this.state.selectValue === '01' && this.opcion1()  }  
                                                    { this.state.selectValue === '02' && this.opcion2()  }
                                                    { this.state.selectValue === '03' && this.opcion3()  }  
                                                    { this.state.selectValue === '04' && this.opcion4()  }       
                                                </div>
                                            }
                                            { this.state.plot5 && 
                                            <div>

                                                <center>
                                                <label className='estadisticas-texto-3'>Personajes Rankeados en Alianza</label>    
                                                <Chart options={
                                                    {
                                                        chart: {
                                                        width: 380,
                                                        type: 'pie',
                                                        },
                                                        labels: this.state.xPlot5,
                                                        responsive: [{
                                                        breakpoint: 480,
                                                        options: {
                                                            chart: {
                                                            width: 200
                                                            },
                                                            legend: {
                                                            position: 'bottom'
                                                            }
                                                        }
                                                        }]
                                                    }
                                                } series={ this.state.yPlot5  } type="pie" width={380} />     
                                                </center>                               
                                            </div>
                                        }                                                                           
                                    </div>     
                                </div>
                            </div>
                        </div>
                    </div>   
                </div>
             
        )
    }
}

export default withRouter(EstadisticasPage)
