import React,{Component} from 'react'
import { withRouter } from 'react-router-dom';
import fotodefecto from '../imagenes/userDefect.png'
import auth from './auth'
import sorcerFoto from '../imagenes/class-selector-sorcerer.png'
import archerFoto from '../imagenes/class-selector-archer.png'
import beserkerFoto from '../imagenes/class-selector-berserker.png'
import gunnerFoto from '../imagenes/class-selector-gunner.png'
import lancerFoto from '../imagenes/class-selector-lancer.png'
import mysticFoto from '../imagenes/class-selector-mystic.png'
import priestFoto from '../imagenes/class-selector-priest.png'
import reaperFoto from '../imagenes/class-selector-reaper.png'
import slayerFoto from '../imagenes/class-selector-slayer.png'
import warriorFoto from '../imagenes/class-selector-warrior.png'
import ipserver from '../config/config'
import Header from './header'

import '../css/miembros.css'



class MiembrosPage extends Component{

    constructor(props){
        super(props)
        this.state = {
            errorText:'',
            data: null, 
            url:fotodefecto,
            nick:'none',
            showResults:false,
            users:[],
            dataUser:null,
            charUser:null
        }
        /**Referencias */
        this.imagen = React.createRef()
        this.lunes = React.createRef()
    }

    async componentDidMount() {
        window.scrollTo(0, 0)
        const {data} = this.props.location.state   
        var foto = (data.data.foto).toString()
        if(  foto === 'defecto' || !foto ){
            this.imagen.current.src = fotodefecto
        }else {
            this.imagen.current.src = data.data.foto
        }
        this.setState({
            data:data,
            nick:data.data.nickname,
        })
        //--------- Traigo todos los usuarios ----------------
        const url = ipserver+'api/user/'
        const response = await fetch(url,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta = await response.json()
        this.setState({  users:respuesta.data  })
    }

    logout(){
        auth.signout()
        this.props.history.push({ pathname: '/' })
    }

    nextPageInicio(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/inicio',
                state: {  data:this.state.data }
        })}
    }

    nextPageEstadisticas(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/estadisticas',
                state: {  data: this.state.data  }
            })
        }
    }

    nextPageGuias(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/guias',
                state: { data: this.state.data }
            })
        }
    }

    nextPageEditarperfil(){
        if(this.state.data){
         this.props.history.push({
                pathname: '/home/editar',
                state: {  data: this.state.data }
        })}
    }

    async detallesDeUsuario(id){
        //--------- Traigo todos los usuarios ----------------
        const url = ipserver+'api/user/'+id.toString()
        const response = await fetch(url,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta = await response.json()
        //--------- Traigo todos los usuarios ----------------
        const url2 = ipserver+'api/char/user/'+id.toString()
        const response2 = await fetch(url2,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta2 = await response2.json()
        this.setState({
            showResults:true,
            dataUser: respuesta.data,
            charUser: respuesta2.data
        })
        window.scrollTo(0,0)
    }

    volverListaPersonajes(){
        this.setState({ showResults:false })
    }

    listaDePersonajes(element,i){
        var charImagen = null
        switch(element.clase){
            case 'Sorccer':
                charImagen = sorcerFoto
                break;
            case 'Archer':
                charImagen = archerFoto
                break;
            case 'Berserker':
                charImagen = beserkerFoto
                break;
            case 'Gunner':
                charImagen = gunnerFoto
                break;
            case 'Lancer':
                charImagen = lancerFoto
                break;
            case 'Mystic':
                charImagen = mysticFoto
                break;
            case 'Priest':
                charImagen = priestFoto
                break;
            case 'Reaper':
                charImagen = reaperFoto
                break;
            case 'Slayer':
                charImagen = slayerFoto
                break;
            case 'Warrior':
                charImagen = warriorFoto
                break;
            default: break;
        }
        return (
            <div key={i}>
                <img className="miembros-image-2"  src={charImagen}  alt='150x150'/>  
                <label className='miembros-texto-7 tab'>   {element.nombre}</label> 
            </div>
        )
    }

    render(){
        return(

            
                <div>
                    <Header/>
                    
                    <div className='miembros-div-1'> 

                        {/* Barra de navegacion  */}
                        <nav className='miembros-nav-1'>     
                            
                            <label title="Pagina principal" className="nav-link miembros-texto-1" onClick={this.nextPageInicio.bind(this)} >Inicio</label>
                            <label title="Lista de Miembros"  className="nav-link miembros-texto-1" >Miembros</label>
                            <label title="Graficos"  className="nav-link miembros-texto-1" onClick={this.nextPageEstadisticas.bind(this)}>Estadisticas</label>
                            <label title="Guias PvE PvP"  className="nav-link miembros-texto-1" onClick={this.nextPageGuias.bind(this)} >Guias</label>
                            <label title="Editar Perfil"  className="nav-link miembros-texto-1" onClick={this.nextPageEditarperfil.bind(this)}>Editar Perfil</label>             
                           
                        </nav>

                         {/* Perfil */}
                         <div className='miembros-div-perfil'> 
                            <center >                                                     
                                <img className='miembros-image-1'  ref={this.imagen}  alt='150x150'/>                                                                          
                                <center>
                                    <p className='miembros-texto-2'>{this.state.nick}</p> 
                                </center>                                                                                                          
                                <button className="btn4 btn-danger fa fa-sign-out" onClick={this.logout.bind(this)} title="Logout">logout</button>  
                            </center> 
                        </div>     
                        
                        
                        {/*Lista de miembros */}
                        <div className='miembros-div-2 border'>
                            {
                                this.state.showResults 
                                ? 
                                <div className='miembros-div-5'>
                                    <div className='miembros-div-6'>
                                        { 
                                            (this.state.dataUser.foto ==='defecto' || this.state.dataUser.foto === '' )
                                            ?  
                                            <div> 
                                                <img className="miembros-image-1" src={fotodefecto} alt="."/>   
                                                <label className='miembros-texto-7 tab'>{this.state.dataUser.nickname}</label> 
                                            </div>
                                            :
                                            <div> 
                                                 <img className="miembros-image-1" src={this.state.dataUser.foto} alt="."/>
                                                <label className='miembros-texto-7 tab'>{this.state.dataUser.nickname}</label> 
                                            </div>
                                           
                                        }                             
                                        <div className='miembros-div-9 border'>
                                            { 
                                                this.state.charUser.map( this.listaDePersonajes.bind(this) ) 
                                            } 
                                        </div>
                                    </div>
                                    
                                    <label className='miembros-texto-7'>Calendario</label>
                                    <div className='miembros-div-7'>
                                        {
                                            this.state.dataUser.L ? 
                                            <div className='miembros-div-8 green' ref={this.lunes}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Lunes</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HL}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.lunes}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Lunes</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HL}</p>
                                                </div>  
                                            </div>
                                        }
                                        {
                                            this.state.dataUser.M ? 
                                            <div className='miembros-div-8 green' ref={this.martes}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Martes</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HM}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.martes}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Martes</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HM}</p>
                                                </div>  
                                            </div>
                                        }
                                        {
                                            this.state.dataUser.MI ? 
                                            <div className='miembros-div-8 green' ref={this.miercoles}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Miercoles</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HMI}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.miercoles}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Miercoles</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HMI}</p>
                                                </div>  
                                            </div>
                                        }
                                        {
                                            this.state.dataUser.J ? 
                                            <div className='miembros-div-8 green' ref={this.jueves}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Jueves</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HJ}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.jueves}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Jueves</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HJ}</p>
                                                </div>  
                                            </div>
                                        }
                                        {
                                            this.state.dataUser.V ? 
                                            <div className='miembros-div-8 green' ref={this.viernes}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Viernes</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HV}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.viernes}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Viernes</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HV}</p>
                                                </div>  
                                            </div>
                                        }
                                        {
                                            this.state.dataUser.S 
                                            ? 
                                            <div className='miembros-div-8 green' ref={this.sabado}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Sabado</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HS}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.sabado}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Sabado</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HS}</p>
                                                </div>  
                                            </div>
                                        }
                                        {
                                            this.state.dataUser.D ? 
                                            <div className='miembros-div-8 green' ref={this.domingo}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Domingo</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HD}</p>
                                                </div>
                                            </div>
                                            :
                                            <div className='miembros-div-8 red' ref={this.domingo}>               
                                                <div>
                                                    <p className='miembros-texto-6'>Domingo</p>
                                                    <p className='miembros-texto-6'>{this.state.dataUser.HD}</p>
                                                </div>  
                                            </div>
                                        }                                  
                                    </div>
                                   
                                    <button className='btn btn-info' onClick={this.volverListaPersonajes.bind(this)}>Volver</button>
                                </div>
                                :
                                <div>
                                  
                                    <div className='miembros-div-3 row border'>
                                        { this.state.users.map((element,i) => {
                                            return (
                                                <div className='col-4 ' key={i}>
                                                    <div className="list-item miembros-div-4 border" >
                                                        <div>                     
                                                            <span className="w-48 avatar gd-primary">
                                                                { 
                                                                    element.foto ==='defecto' || element.foto ===''
                                                                    ?  
                                                                    <img className="miembros-image-1" src={fotodefecto} alt="."/>
                                                                    :
                                                                    <img className="miembros-image-1" src={element.foto} alt="."/>
                                                                }           
                                                            </span>                 
                                                        </div>
                                                        <div className="flex">
                                                            <label  className="miembros-texto-4" onClick={ ()=>{ this.detallesDeUsuario(element.id)} }>
                                                                {element.nickname}
                                                            </label>
                                                        </div>
                                                        <div className="no-wrap">
                                                            <label  className='miembros-texto-5' > Miembro </label>
                                                        </div>
                                                    </div>
                                                </div>
                                            )         
                                        })}
                                    </div>
                                </div>
                            }
                        </div>      
                    </div> 
                </div>
            
        )
    }
}

export default withRouter(MiembrosPage)
