import React,{Component} from 'react'
import { withRouter } from 'react-router-dom';
import fotodefecto from '../imagenes/userDefect.png'
import auth from './auth'
import '../css/inicio.css'
import ipserver from '../config/config'
import Header from './header'


/**Constantes */
var intervalId = 0

class InicioPage extends Component{

    constructor(props){
        super(props)
        this.state = {     
            data: null, 
            url:fotodefecto,
            nick:'none',
            showResults:false,
            titulo:'',
            descripcion:'',
            eventos:[],
            idEvent:0
        }
        /**Referencias */
        this.imagen = React.createRef()
        this.duracion = React.createRef()
        this.fecha = React.createRef()
        this.select = React.createRef()
        this.nuevoDiv = React.createRef()
        this.descripcion = React.createRef()
        this.titulo = React.createRef()
        this.botonTerminar = React.createRef()
    }

    async componentDidMount(){
        window.scrollTo(0, 0)
        const {data} = this.props.location.state    
        var foto = (data.data.foto).toString()
        if(  (foto === 'defecto' || !foto )  ){
            this.imagen.current.src = fotodefecto
        }else {
            this.imagen.current.src = data.data.foto
        }
        this.setState({
            data:data,
            nick:data.data.nickname,
        })
        /**Traigo eventos del servidor */
        const url = ipserver+'api/event/'
        const response = await fetch(url,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta = await response.json()
        this.setState({
            eventos:respuesta.data
        })
        intervalId = setInterval( () => {
                this.setState({})
            },1000    
        )
    }

    logout(){
        clearInterval(intervalId);
        auth.signout()
        this.props.history.push({  pathname: '/' })
    }

    mostrarVentanaCrearEvento() { 

        if(this.state.data.data.nickname === 'Invitado'){
            alert('Los invitados no puden iniciar eventos')
            return 
        }

        this.setState({ showResults: true });
    }

    dhm(ms,id){
        var days = Math.floor(ms / (24*60*60*1000));
        var daysms=ms % (24*60*60*1000);
        var hours = Math.floor((daysms)/(60*60*1000));
        var hoursms=ms % (60*60*1000);
        var minutes = Math.floor((hoursms)/(60*1000));
        var minutesms=ms % (60*1000);
        var sec = Math.floor((minutesms)/(1000));

        var formattedSeconds = ("0" + sec).slice(-2);
        var formattedMinutes = ("0" + minutes).slice(-2);
        var formattedHours = ("0" + hours).slice(-2);
        var formattedDays = ("0" + days).slice(-2);

        if(formattedMinutes < 0 || formattedHours <0 || formattedDays < 0 ){
            if(minutes < 30 ){
                this.terminarEvento(id)
            }
            return 'TERMINO'
        }
        return formattedDays+":"+formattedHours+":"+formattedMinutes+":"+formattedSeconds;
    }

    selectChange(){
        switch(this.select.current.value){
            case 'Fecha':
                this.duracion.current.disabled = true
                this.duracion.current.type = "hidden"
                this.fecha.current.disabled = false
                this.fecha.current.type = "datetime-local"
                break;
            case 'Duracion':
                this.duracion.current.disabled = false
                this.duracion.current.type = "time"
                this.fecha.current.disabled = true
                this.fecha.current.type = "hidden"
                break;
            default: 
                break;
        }
    }

    async creandoEvento(){
        /**Verificando campos */
        if(this.titulo.current.value.length < 1 ){
            alert('Ingresa un Titulo')
            return
        }
        if(this.descripcion.current.value.length < 1 ){
            alert('Ingresa una breve descripcion')
            return
        }
        if(this.titulo.current.value.length > 36 ){
            alert('Titulo demasiado largo max 36 caracteres')
            return
        }
        if(this.descripcion.current.value.length > 120 ){
            alert('Descripcion demasiada larga max 120 caracteres')
            return
        }
        var time = ''
        if(!this.duracion.current.disabled){
            if(!this.duracion.current.value) {
                alert('Introduce una duracion de evento')
                return
            }
            var hours = this.duracion.current.value.split(":")[0];
            var minutes = this.duracion.current.value.split(":")[1];

            var numberMinutos = parseInt(minutes, 10);
            var numberHoras = parseInt(hours, 10);

            var date = new Date()
            date.setHours(date.getHours() + numberHoras);
            date.setMinutes(date.getMinutes() + numberMinutos);

           /* var dia = date.getDate()
            var mes = date.getUTCMonth() +1 
            var anio = date.getFullYear()
            var minuto = date.getMinutes()
            var hora = date.getHours()

            var formattedMinutes = ("0" + minuto).slice(-2);
            var formattedHours = ("0" + hora).slice(-2);

            time = mes+'/'+dia+'/'+anio+' '+formattedHours+':'+formattedMinutes+':00'*/

            time = date.toString()
        }

        if(!this.fecha.current.disabled){
            if(!this.fecha.current.value) {
                alert('Introduce una fecha de evento')
                return
            }
            var date2 = new Date(this.fecha.current.value )
            if( date2 < new Date() ){
                alert('Fecha introducida invalida. Fecha antigua')
                return
            }else{
               /* var minuto2 = date2.getMinutes()
                var hora2 = date2.getHours()
                var dia2 = date2.getDate()
                var mes2 = date2.getUTCMonth() +1 
                var anio2 = date2.getFullYear()
    
                var formattedMinutes2 = ("0" + minuto2).slice(-2);
                var formattedHours2 = ("0" + hora2).slice(-2);
               
                time = mes2+'/'+dia2+'/'+anio2+' '+formattedHours2+':'+formattedMinutes2+':00'*/
                time = date2.toString()
            }
           
        }

       // console.log('Fecha q mando al servidor')
       // console.log(time)
        
        /**Enviando al servidor */
        var query = JSON.stringify({ 
            "name_user": this.state.data.data.nickname,
            "titulo":this.titulo.current.value ,
            "time": time,
            "descripcion":this.descripcion.current.value    
        })
        const url = ipserver+'api/event/'
        const response = await fetch(url,{
            method: 'post' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: query   
        })
        const data = await response.json()
        if(data.status === 0){
            this.updateEvents()
            this.setState({ showResults: false });
        }else{
            alert('Ocurrio un error al crear el evento')
        }  
    }

    verEventos(){
        this.setState({ showResults: false });
    }

    async terminarEvento(id){

        if(this.state.data.data.nickname === 'Invitado'){
            alert('Los invitados no puden Terminar eventos')
            return 
        }
        
        if(this.state.eventos.length < 1 ){
            alert('No hay eventos para finalizar')
        }else{
            const url = ipserver+'api/event/'+id.toString()
            const response = await fetch(url,{
                method: 'delete' ,
                headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
                }
            })
            const data = await response.json()
            console.log(data)
            this.updateEvents()
        }
    }

    sliderfuncion(element,i){

        if(i===0){
            
            return (       
                <div key={i} className="carousel-item active  inicio-div-carousel-1">          
                    <h1 className='inicio-texto-3'> {element.titulo}</h1>
                    <h1 className='inicio-texto-4' >
                        { this.dhm(new Date(element.time).getTime() - new Date().getTime(),element.id ) }
                    </h1>          
                    <p className='inicio-texto-5'> {element.descripcion} </p>
                   <center>
                    <button title='Terminar cuenta regresiva' ref={this.botonTerminar} className="btn btn-danger border" onClick={() => this.terminarEvento(element.id)}>Terminar</button>
                    <button title='Crear cuenta regresiva' className="btn btn-success border" onClick={this.mostrarVentanaCrearEvento.bind(this)}>Nuevo</button>           
                   </center>
                </div>
            )
        }else{
            return (
               
                <div key={i} className="carousel-item inicio-div-carousel-1">          
                <h1 className='inicio-texto-3'> {element.titulo}</h1>
                <h1 className='inicio-texto-4' >
                    { this.dhm(new Date(element.time).getTime() - new Date().getTime(),element.id ) }
                </h1>          
                <p className='inicio-texto-5'> {element.descripcion} </p>
                <center>
                    <button title='Terminar cuenta regresiva' ref={this.botonTerminar} className="btn btn-danger border" onClick={() => this.terminarEvento(element.id)}>Terminar</button>
                    <button title='Crear cuenta regresiva' className="btn btn-success border" onClick={this.mostrarVentanaCrearEvento.bind(this)}>Nuevo</button>           
                </center>
            </div>
            )
        }
    }

    async updateEvents(){
        /**Traigo eventos del servidor */
        const url = ipserver+'api/event/'
        const response = await fetch(url,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta = await response.json()
        //console.log(respuesta)
        this.setState({
            eventos:respuesta.data
        })
    }

    nextPageMiembros(){
        clearInterval(intervalId);
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/miembros',
            state: { data: this.state.data }
        })}
    }

    nextPageEstadisticas(){
        clearInterval(intervalId);
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/estadisticas',
            state: { data: this.state.data }
        })}
    }

    nextPageGuias(){
        clearInterval(intervalId);
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/guias',
            state: { data: this.state.data }
        })}
    }

    nextPageEditarperfil(){
        clearInterval(intervalId);
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/editar',
            state: {   data: this.state.data }
        })}
    }

    render(){
        return(  
                <div> 
                    <Header/>
                    <div className='inicio-div-1'>

                        {/* Barra de navegacion  */}
                        <nav className='inicio-nav-1'>                                 
                            <label title="Pagina principal" className='nav-link inicio-texto-1' >Inicio</label>
                            <label title="Lista de Miembros"  className='nav-link inicio-texto-1' onClick={this.nextPageMiembros.bind(this)} >Miembros</label>
                            <label title="Graficos"  className='nav-link inicio-texto-1' onClick={this.nextPageEstadisticas.bind(this)} >Estadisticas</label>
                            <label title="Guias PvE PvP"  className='nav-link inicio-texto-1' onClick={this.nextPageGuias.bind(this)}>Guias</label>
                            <label title="Editar Perfil"  className='nav-link inicio-texto-1' onClick={this.nextPageEditarperfil.bind(this)}>Editar Perfil</label>                   
                        </nav>

                         {/* Perfil */}
                         <div className='inicio-div-perfil'> 
                            <center >                                                     
                                <img className='inicio-image-1'  ref={this.imagen}  alt='150x150'/>                                                                          
                                <center>
                                    <p className='inicio-texto-2'>{this.state.nick}</p> 
                                </center>                                                                                                          
                                <button className="btn4 btn-danger fa fa-sign-out" onClick={this.logout.bind(this)} title="Logout">logout</button>  
                            </center> 
                        </div>   

                        {/* Contador */}
                        <div className='inicio-div-2 border'>
                            {
                                this.state.showResults ?
                                <div className='inicio-div-3 border'>
                                   
                                        <input type="text" ref={this.titulo} className="form-control mb-1" placeholder="Titulo"/>
                                        <input type="text" ref={this.descripcion} className="form-control mb-1" placeholder="Descripcion"/>
                                       
                                        <select id="select" ref={this.select} name="select" className="custom-select fa" onChange={this.selectChange.bind(this)}>
                                                <option >Duracion</option>
                                                <option >Fecha</option> 
                                        </select><br/>

                                        <input type="hidden" disabled ref={this.fecha} className='form-control mb-1' placeholder="Fecha"/>
                                        <input type="time"  ref={this.duracion} className='form-control mb-1' placeholder="Duracion"/>  
                                        <br/><br/>                                   
                                        <button className="btn btn-success border" onClick={this.creandoEvento.bind(this)} >Crear Evento</button>
                                        <button className="btn btn-secondary border " onClick={this.verEventos.bind(this)} >Volver</button>
                                         
                                </div>
                                :
                                <div>
                                    {
                                        this.state.eventos.length === 0 ?
                                        <div >       
                                            
                                            <button title='Crear cuenta regresiva' className="btn btn-success border" onClick={this.mostrarVentanaCrearEvento.bind(this)}>
                                                Crea un nuevo Evento
                                            </button>   
                                            
                                        </div>
                                        :
                                        <div>
                                        <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
                                                <div className="carousel-inner">
                                                    { this.state.eventos.map( this.sliderfuncion.bind(this) )  }
                                                </div>
                                        </div>
                                        <div className='inicio-div-4'>
                                            <a className='App-slider-button' href="#carouselExampleControls" role="button" data-slide="prev">
                                                <span className="carousel-control-prev-icon" aria-hidden="false"></span>
                                                <span className="sr-only">Previous</span>
                                            </a>
                                            <a className='App-slider-button' href="#carouselExampleControls" role="button" data-slide="next">
                                                <span className="carousel-control-next-icon" aria-hidden="false"></span>
                                                <span className="sr-only">Next</span>
                                            </a>
                                        </div>
                                    </div>    
                                    }                                                  
                                </div>
                            }
                        </div>  

                       

                    </div>
                </div>
            
        )
    }
}

export default withRouter(InicioPage)
