import React,{Component} from 'react'
import { withRouter, Link } from 'react-router-dom';
import auth from './auth'
import ipserver from '../config/config';
import '../css/login.css'


class HomeLogin extends Component{

    constructor(props){
        super(props)
        /**Referencias */
        this.email = React.createRef()
        this.pass = React.createRef()
        /*Store the previous pathname and search strings*/
        this.currentPathname = null;
        this.currentSearch = null;
    }

    
    async entrarPageInvitado(){

        var query = JSON.stringify({ 
            "mail": 'Invitado@mail.com',
            "pass": 'Invitado123'
        })

        const url = ipserver+'api/user/login'
        const response = await fetch(url,{
            method: 'post' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: query   
        })
      
        const data = await response.json()

        if( data.status === 1){
            auth.login( ()=>{
                this.props.history.push({
                    pathname: '/home/inicio',
                    state: {  data:data  }
                })
            } )
        }else{
            alert('Combinacion incorrecta de E-mail y Contraseña')
        }
    }


    async entrarPage(){

        var query = JSON.stringify({ 
            "mail": this.email.current.value,
            "pass": this.pass.current.value
        })

        const url = ipserver+'api/user/login'
        const response = await fetch(url,{
            method: 'post' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: query   
        })
      
        const data = await response.json()

        if( data.status === 1){
            auth.login( ()=>{
                this.props.history.push({
                    pathname: '/home/inicio',
                    state: {  data:data  }
                })
            } )
        }else{
            alert('Combinacion incorrecta de E-mail y Contraseña')
        }
    }

    clearError(){
        this.setState({ errorText: '' })
    }

    componentDidMount() {

        window.scrollTo(0, 0)

       /* const {beso} = this.props.location.state    
        console.log(beso)*/

        /*.props.history.listen((newLocation, action) => {
            if (action === "PUSH") {
                if (
                newLocation.pathname !== this.currentPathname ||
                newLocation.search !== this.currentSearch
                ) {
                // Save new location
                this.currentPathname = newLocation.pathname;
                this.currentSearch = newLocation.search;
    
                // Clone location object and push it to history
                this.props.history.push({
                    pathname: newLocation.pathname,
                    search: newLocation.search
                });
                }
            } else {
                // Send user back if they try to navig ate back
                this.props.history.go(1);
            }
        })*/

    }

    render(){

        return(
           
          
           
                    <div className='login-div-1'>
                        <form >
                            <h2 className='login-texto-1'>Login</h2>
                            <p className='login-texto-2'>Unete a nuestra lista de miembros.</p>
                            <input type="email" ref={ this.email }  className="form-control mb-4" placeholder="E-mail" onChange={this.clearError.bind(this)}/>
                            <input type="password" ref={ this.pass }  className="form-control mb-4" placeholder="Contraseña" onChange={this.clearError.bind(this)}/>
                            <button type="button" className="btn btn-info btn-block" onClick={this.entrarPage.bind(this)} >Entrar</button>                  
                            <Link to='/register' className='login-texto-3'>Registrate Ahora</Link>          
                        </form>
                        <br/><br/>
                        <button type="button" className="btn btn-danger btn-block" onClick={this.entrarPageInvitado.bind(this)} >Ingresa como invitado</button>                  
                          
                    </div>  
           
        

        )
    }
}

export default withRouter(HomeLogin)