import React from 'react';
import{BrowserRouter,Route,Switch,Redirect} from 'react-router-dom'
import HomeLogin from './componentes/loginPage'
import InicioPage from './componentes/inicioPage'
import MiembrosPage from './componentes/miembrosPage'
import EstadisticasPage from './componentes/estadisticasPage'
import GuiasPage from './componentes/guiasPage'
import EditarperfilPage from './componentes/editarperfilPage'
import RegisterPage from './componentes/registerPage'
import PrivateRoute from './componentes/protectedRoute'

import './App.css';



function App() {
  return (

    <BrowserRouter>
      <div className='App-div-1'>
        <div className='App-div-2'>
          <div className='App-div-3'>
            
              <Switch>   
                <Route exact path='/' component ={HomeLogin} />
                <PrivateRoute exact path='/home/inicio' component ={InicioPage}/>  
                <PrivateRoute exact path='/home/miembros' component ={MiembrosPage}/>  
                <PrivateRoute exact path='/home/estadisticas' component ={EstadisticasPage}/>  
                <PrivateRoute exact path='/home/guias' component ={GuiasPage}/>
                <PrivateRoute exact path='/home/editar' component ={EditarperfilPage}/>  
                <Route exact path='/register' component ={RegisterPage}/>
                <Route render={() => <Redirect to={{pathname: "/"}} />} />
              </Switch>
         
          </div>
        </div>
      </div>
    </BrowserRouter>
    
  );
}

export default App;

