import React,{Component} from 'react'
import { withRouter } from 'react-router-dom';
import fotodefecto from '../imagenes/userDefect.png'
import auth from './auth'
import '../css/guias.css'
import ipserver from '../config/config'
import Header from './header'


class GuiasPage extends Component{

    constructor(props){
        super(props)
        this.state = {
            data:null,
            nick:'none',
            guias:[],
            publicarShow:true,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false,
            guiasSorccer:[],
            guiasArcher:[],
            guiasGunner:[],
            guiasReaper:[],
            guiasSlayer:[],
            guiasWarrior:[],
            guiasLancer:[],
            guiasPriest:[],
            guiasMystic:[],
            guiasBerserker:[],
            guiasInstancias:[],
            guiasOtro:[],
            mensaje:''
        }

        /**Referencias */
        this.imagen = React.createRef()
        this.select = React.createRef()
        this.titulo = React.createRef()
        this.url = React.createRef()
        this.descripcion = React.createRef()
    }

    async componentDidMount() {
        window.scrollTo(0, 0)
        const {data} = this.props.location.state   
        var foto = (data.data.foto).toString()
        if(  foto === 'defecto' || !foto  ){
            this.imagen.current.src = fotodefecto
        }else {
            this.imagen.current.src = data.data.foto
        }
        //--------- Traigo todos los usuarios ----------------

        const url2 = ipserver+'api/guide/'
        const response2 = await fetch(url2,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }   
        })
        const respuesta2 = await response2.json()

        if( respuesta2.status === 0){
            this.setState({
                data:data,
                nick:data.data.nickname,
                guias:respuesta2.data
            })
        }
        this.separarPorCategorias()
    }

    separarPorCategorias(){
        var guiasSorccer= []
        var guiasArcher= []
        var guiasGunner= []
        var guiasReaper= []
        var guiasSlayer= []
        var guiasWarrior= []
        var guiasLancer= []
        var guiasPriest= []
        var guiasMystic= []
        var guiasBerserker= []
        var guiasInstancias= []
        var guiasOtro= []

        this.state.guias.map( (e,i)=>{
            if(e.categoria === 'Sorccer') guiasSorccer.push(e)
            if(e.categoria === 'Archer') guiasArcher.push(e)
            if(e.categoria === 'Gunner') guiasGunner.push(e)
            if(e.categoria === 'Reaper') guiasReaper.push(e)
            if(e.categoria === 'Slayer') guiasSlayer.push(e)
            if(e.categoria === 'Warrior') guiasWarrior.push(e)
            if(e.categoria === 'Lancer') guiasLancer.push(e)
            if(e.categoria === 'Priest') guiasPriest.push(e)
            if(e.categoria === 'Mystic') guiasMystic.push(e)
            if(e.categoria === 'Berserker') guiasBerserker.push(e)
            if(e.categoria === 'Instancias') guiasInstancias.push(e)
            if(e.categoria === 'Otro') guiasOtro.push(e)   
            return 0
        } )

        this.setState({
            guiasSorccer,
            guiasArcher,
            guiasGunner,
            guiasReaper,
            guiasSlayer,
            guiasWarrior,
            guiasLancer,
            guiasPriest,
            guiasMystic,
            guiasBerserker,
            guiasInstancias,
            guiasOtro
        })
    }

    logout(){
        auth.signout()
        this.props.history.push({ pathname: '/' })
    }
 
    nextPageInicio(){    
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/inicio',
                state: {  data:this.state.data }
        })}
    }

    nextPageMiembros(){
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/miembros',
            state: { data: this.state.data }
        })}
    }

    nextPageEstadisticas(){
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/estadisticas',
            state: { 
                data: this.state.data
            }
        })}
    }

    nextPageEditarperfil(){
        if(this.state.data){
        this.props.history.push({
            pathname: '/home/editar',
            state: { data: this.state.data }
        })}
    }

    comprobarCampos(){
        this.setState({
            mensaje:''
        })
        if( this.titulo.current.value === ''){
            alert('El titulo no puede estar vacio')     
            return false
        }else if(  this.titulo.current.value.length > 60 ){
            alert('El titulo no puede tener mas de 60 caracteres')     
            return false
        }
        if( this.url.current.value === ''){
            alert('La URL no puede estar vacia')     
            return false
        }
        if( this.descripcion.current.value === ''){
            alert('Escribe una breve descripcion')     
            return false
        }else if(  this.descripcion.current.value.length > 180 ){
            alert('La descripcion no puede tener mas de 150 caracteres')     
            return false
        }
        if( this.select.current.value === 'Elige una categoria'){
            alert('Selecciona una categoria')     
            return false
        }
        return true
    }

    async publicarGuia(){

        if(this.state.data.data.nickname === 'Invitado'){
            alert('Los invitados no puden publicar Guias')
            return 
        }


        if( this.comprobarCampos() ){
            var query = JSON.stringify({ 
                "id_user": this.state.data.data.id,
                "titulo": this.titulo.current.value,
                "url": this.url.current.value,
                "descripcion": this.descripcion.current.value,
                "categoria":this.select.current.value    
              })
            const url = ipserver+'api/guide/'
            const response = await fetch(url,{
                method: 'post' ,
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
                body: query   
              })
            const data = await response.json()
            //console.log(data)
            if(data.status === 0){
                this.titulo.current.value = ''
                this.url.current.value = ''
                this.descripcion.current.value = ''
                this.select.current.value = 'Elige una categoria'
                this.setState({
                    mensaje:'Tu Guia fue publicada Exitosamente !'
                })
            }else{
                this.setState({
                    mensaje:'Ocurrio un error y no se pudo publicar'
                })
            }
        } 
    }

    otraGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            slayerrShow:false,
            reapeShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:true
        }) 
    }

    instanciasGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            slayerrShow:false,
            reapeShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:true,
            otrasShow:false
        }) 
    }

    berserkerGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            slayerrShow:false,
            reapeShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:true,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    mysticGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:true,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    priestGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:true,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    lancerGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:true,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    warriorGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:true,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    slayerGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:true,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    reaperGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:true,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    gunnerGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:true,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    archerGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:false,
            archerShow:true,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    sorccerGuia(){
        this.setState({
            publicarShow:false,
            sorccerShow:true,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    crearGuia(){
        this.setState({
            publicarShow:true,
            sorccerShow:false,
            archerShow:false,
            gunnerShow:false,
            reaperShow:false,
            slayerShow:false,
            warriorShow:false,
            lancerShow:false,
            priestShow:false,
            mysticShow:false,
            berserkerShow:false,
            instanciasShow:false,
            otrasShow:false
        }) 
    }

    listaDeGuias(){
        var lista = []
        if(this.state.sorccerShow) lista = this.state.guiasSorccer  
        if(this.state.archerShow) lista = this.state.guiasArcher 
        if(this.state.gunnerShow) lista = this.state.guiasGunner 
        if(this.state.reaperShow) lista = this.state.guiasReaper 
        if(this.state.slayerShow) lista = this.state.guiasSlayer 
        if(this.state.warriorShow) lista = this.state.guiasWarrior 
        if(this.state.lancerShow) lista = this.state.guiasLancer 
        if(this.state.priestShow) lista = this.state.guiasPriest 
        if(this.state.mysticShow) lista = this.state.guiasMystic 
        if(this.state.berserkerShow) lista = this.state.guiasBerserker 
        if(this.state.instanciasShow) lista = this.state.guiasInstancias 
        if(this.state.otrasShow) lista = this.state.guiasOtro 
        return(
            <div className='guias-div-4' >
                {
                    lista.length === 0 ?
                    
                    <h1 className='guias-texto-4 '> No hay Guias para mostrar </h1>
                   
                    :
                    <div className='guias-div-6'>
                        <br/><br/>
                        <div className=''>
                            { lista.map((element,i) => {
                                return (
                                    <div key={i} className='guias-div-7'>
                                        <div className='guias-texto-3'>{element.titulo}</div>
                                        <div><a target="_blank" rel="noopener noreferrer" href={element.url} className=''>Link</a></div>
                                        <div><label className=''>{element.descripcion}</label></div>
                                    </div> 
                                )           
                            })}
                        </div>
                    </div>
                }  
            </div>
        )
    }

    render(){

        return(

                <div> 
                    <Header/>
                    <div className='guias-div-1'>
              
                    {/* Barra de navegacion  */}             
                    <nav className='guias-nav-1'>              
                        <label title="Pagina principal" className="nav-link guias-texto-1" onClick={this.nextPageInicio.bind(this)} >Inicio</label>
                        <label title="Lista de Miembros"  className="nav-link guias-texto-1" onClick={this.nextPageMiembros.bind(this)} >Miembros</label>
                        <label title="Graficos"  className="nav-link guias-texto-1" onClick={this.nextPageEstadisticas.bind(this)} >Estadisticas</label>
                        <label title="Guias PvE PvP"  className="nav-link guias-texto-1" >Guias</label>
                        <label title="Editar Perfil"  className="nav-link guias-texto-1" onClick={this.nextPageEditarperfil.bind(this)}>Editar Perfil</label>                                        
                    </nav>

                {/* Perfil */}
                <div className='guias-div-perfil'> 
                    <center >                                                     
                        <img className='guias-image-1'  ref={this.imagen}  alt='150x150'/>                                                                          
                        <center>
                            <p className='guias-texto-2'>{this.state.nick}</p> 
                        </center>                                                                                                          
                        <button className="btn4 btn-danger fa fa-sign-out" onClick={this.logout.bind(this)} title="Logout">logout</button>  
                    </center> 
                </div>


                {/**Guias */}
                <div className='guias-div-2 border'>
                    <div className='guias-div-3 border'>          
                        
                        <label className="btn btn-success guias-label-2" onClick={()=>{this.crearGuia()}} >Publicar Guia</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.sorccerGuia()}} >Sorccer</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.archerGuia()}} >Archer</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.gunnerGuia()}} >Gunner</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.reaperGuia()}} >Reaper</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.slayerGuia()}} >Slayer</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.warriorGuia()}} >Warrior</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.lancerGuia()}} >Lancer</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.priestGuia()}} >Priest</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.mysticGuia()}} >Mystic</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.berserkerGuia()}} >Berserker</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.instanciasGuia()}} >Instancias</label>
                        <label className="btn btn-info guias-label-1" onClick={()=>{this.otraGuia()}} >Otro</label>          
                                                                        
                    </div>
                    <div >   
                        { this.state.publicarShow ?  
                            <div className='guias-div-4'>
                               
                                <p className='guias-texto-3'>Publica tu Guia</p>
                             
                                <label>
                                    1- Crea tu propia guia utilizando por ejemplo Google docs, en el siguiente  
                                    link hay un breve tutorial para crear un documento online en google drive -
                                     <a target="_blank" rel="noopener noreferrer" href="https://tecnologia-facil.com/como-hacer/como-crear-un-documento-en-google-docs/">
                                        Link
                                    </a><br/>
                                    2- Una vez terminada tu Guia copia el link de tu documento y pegalo en el campo
                                    URL, no te olvides de completar los demas campos respetando el contenido de tu guia<br/>
                                    3- Si quieres publicar la guia de alguien mas simplemente coloca el link de la pagina donde se encuentra
                                    y completa los campos respetando el contenido de la misma
                                </label>

                                <div className="guias-div-5 border">
                                    <input type='text' className='form-control mb-1 guias-label-3' ref={this.titulo} placeholder="Titulo"/>
                                    <input type='text' className='form-control mb-1 guias-label-3' ref={this.url} placeholder="URL"/>
                                    <textarea placeholder="Descripcion" className='form-control mb-1 guias-label-3' ref={this.descripcion} />
                                    <select id="select" className='form-control mb-1 guias-label-3' placeholder="Elije una categoria" ref={this.select} name="select"  >
                                        <option >Elige una categoria</option>
                                        <option >Sorccer</option>
                                        <option >Archer</option>
                                        <option >Gunner</option>
                                        <option >Reaper</option>
                                        <option >Slayer</option>
                                        <option >Warrior</option>
                                        <option >Lancer</option>
                                        <option >Priest</option>
                                        <option >Mystic</option>
                                        <option >Berserker</option>
                                        <option >Instancias</option>
                                        <option >Otro</option>
                                    </select><br/>
                                    <button className='btn btn-success' type='btn btn-success' onClick={this.publicarGuia.bind(this)} >Crear</button>
                                    <p className='blue'>{this.state.mensaje}</p>
                                </div>    
                            </div>
                            :
                            this.listaDeGuias()
                        }
                    </div>
                </div>
            </div> 

                </div>
                 
        )
    }
}

export default withRouter(GuiasPage)