import React,{Component} from 'react'
import { withRouter } from 'react-router-dom';
import fotodefecto from '../imagenes/userDefect.png'
import auth from './auth'
import '../css/editar.css'
import {storage} from "../Firebase/index";
import ipserver from '../config/config'
import Header from './header'


class EditarperfilPage extends Component{

    constructor(props){
        super(props)
        this.state = {
            data:null,
            nick:'none',
            users:[],
            char:null,
            mensaje:'',
            image2:null,
            url:null,
            slidebarValue:1, // nivel
        }
        /**Referencias */
        this.divscroll = React.createRef() 
        this.imagen = React.createRef()
        this.imagen2 = React.createRef()
        this.nivelrange = React.createRef();
        this.nicknameInput = React.createRef();
        this.emailInput = React.createRef();
        this.passInput = React.createRef();
        this.nombreInput = React.createRef();
        this.claseInput = React.createRef();
        this.alianzaInput = React.createRef();
        this.timeLunes = React.createRef()
        this.timeMartes = React.createRef()
        this.timeMiercoles = React.createRef()
        this.timeJueves = React.createRef()
        this.timeViernes = React.createRef()
        this.timeSabado = React.createRef()
        this.timeDomingo = React.createRef()
        this.lunes = React.createRef()
        this.martes = React.createRef()
        this.miercoles = React.createRef()
        this.jueves = React.createRef()
        this.viernes = React.createRef()
        this.sabado = React.createRef()
        this.domingo = React.createRef()
        this.arma = React.createRef();
        this.armadura = React.createRef();
        this.botas = React.createRef();
        this.guantes = React.createRef();
        this.cinturon = React.createRef();
    }

    async componentDidMount() {
        window.scrollTo(0, 0)
        const {data} = this.props.location.state   
        var foto = (data.data.foto).toString()
        if(  foto === 'defecto' || !foto  ){
            this.imagen.current.src = fotodefecto
        }else {
            this.imagen.current.src = data.data.foto
        }
      
        const url = ipserver+'api/char/user/'+data.data.id
        const response = await fetch(url,{
            method: 'get' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }  
          })
        const res = await response.json()
        this.setState({
            data:data,
            nick:data.data.nickname,
            char:res.data[0]  ,
            slidebarValue:res.data[0].nivel,
            url: data.data.foto
        })
        this.inicializacion()
    }

    inicializacion(){
        /**Configuracionn inicial */
        this.timeLunes.current.disabled = !this.state.data.data.L
        this.timeLunes.current.value = this.state.data.data.HL
        this.timeMartes.current.disabled = !this.state.data.data.M
        this.timeMartes.current.value = this.state.data.data.HM
        this.timeMiercoles.current.disabled = !this.state.data.data.MI
        this.timeMiercoles.current.value = this.state.data.data.HMI
        this.timeJueves.current.disabled = !this.state.data.data.J
        this.timeJueves.current.value = this.state.data.data.HJ
        this.timeViernes.current.disabled = !this.state.data.data.V
        this.timeViernes.current.value = this.state.data.data.HV
        this.timeSabado.current.disabled = !this.state.data.data.S
        this.timeSabado.current.value = this.state.data.data.HS
        this.timeDomingo.current.disabled = !this.state.data.data.D
        this.timeDomingo.current.value = this.state.data.data.HD
        this.lunes.current.checked = this.state.data.data.L
        this.martes.current.checked = this.state.data.data.M
        this.miercoles.current.checked = this.state.data.data.MI
        this.jueves.current.checked = this.state.data.data.J
        this.viernes.current.checked = this.state.data.data.V
        this.sabado.current.checked = this.state.data.data.S
        this.domingo.current.checked = this.state.data.data.D
        this.nicknameInput.current.value= this.state.nick
        this.emailInput.current.value= this.state.data.data.mail
        this.passInput.current.value= this.state.data.data.pass
        this.nombreInput.current.value= this.state.char.nombre
        this.claseInput.current.value= this.state.char.clase
        this.nivelrange.current.value = this.state.char.nivel
        this.arma.current.value = this.state.char.arma
        this.armadura.current.value = this.state.char.armadura
        this.botas.current.value = this.state.char.botas
        this.guantes.current.value = this.state.char.guantes
        this.cinturon.current.value = this.state.char.cinturon
        if(this.state.char.rankeado) this.alianzaInput.current.value= 'Si'
        else  this.alianzaInput.current.value= 'No'
        if( (this.state.data.data.foto === 'defecto') || (!this.state.data.data.foto)) this.imagen2.current.src = fotodefecto
        else this.imagen2.current.src = this.state.data.data.foto
    }

    logout(){ 
        auth.signout()
        this.props.history.push({ pathname: '/'})
    }
    
    rangeNivel(){
        this.setState({slidebarValue: this.nivelrange.current.value})
    }
 
    nextPageInicio(){    
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/inicio',
                state: { data: this.state.data }
            })
        }
    }

    nextPageMiembros(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/miembros',
                state: {  data: this.state.data }
            })
        }
    }

    nextPageEstadisticas(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/estadisticas',
                state: {  data: this.state.data}
            })
        }
    }

    nextPageGuias(){
        if(this.state.data){
            this.props.history.push({
                pathname: '/home/guias',
                state: {   data: this.state.data }
            })
        }
    }

    checkboxOnChange(event,key){
        switch(key){
            case 0:
                if(this.lunes.current.checked){
                    this.timeLunes.current.disabled = false
                    this.timeLunes.current.value='12:00'
                    this.timeLunes.current.style.color = 'green'
                }else {
                    this.timeLunes.current.disabled = true
                    this.timeLunes.current.value=''
                    this.timeLunes.current.style.color = 'black'
                }   
                this.setState({lunesBox:event.target.checked})
                break
            case 1:
                if(this.martes.current.checked){
                    this.timeMartes.current.disabled = false
                    this.timeMartes.current.value='12:00'
                    this.timeMartes.current.style.color = 'green'
                }else {
                    this.timeMartes.current.disabled = true
                    this.timeMartes.current.value=''
                    this.timeMartes.current.style.color = 'black'
                }
                this.setState({martesBox:event.target.checked})
                break
            case 2:
                if(this.miercoles.current.checked){
                    this.timeMiercoles.current.disabled = false
                    this.timeMiercoles.current.value='12:00'
                    this.timeMiercoles.current.style.color = 'green'
                }else {
                    this.timeMiercoles.current.disabled = true
                    this.timeMiercoles.current.value=''
                    this.timeMiercoles.current.style.color = 'black'
                }
                this.setState({miercolesBox:event.target.checked})
                break
            case 3:
                if(this.jueves.current.checked){
                    this.timeJueves.current.disabled = false
                    this.timeJueves.current.value='12:00'
                    this.timeJueves.current.style.color = 'green'
                }else {
                    this.timeJueves.current.disabled = true
                    this.timeJueves.current.value=''
                    this.timeJueves.current.style.color = 'black'
                }
                this.setState({juevesBox:event.target.checked})
                break    
            case 4:
                if(this.viernes.current.checked){
                    this.timeViernes.current.disabled = false
                    this.timeViernes.current.value='12:00'
                    this.timeViernes.current.style.color = 'green'
                }else {
                    this.timeViernes.current.disabled = true
                    this.timeViernes.current.value=''
                    this.timeViernes.current.style.color = 'black'
                }
                this.setState({viernesBox:event.target.checked})
                break
            case 5:
                if(this.sabado.current.checked){
                    this.timeSabado.current.disabled = false
                    this.timeSabado.current.value='12:00'
                    this.timeSabado.current.style.color = 'green'
                }else {
                    this.timeSabado.current.disabled = true
                    this.timeSabado.current.value=''
                    this.timeSabado.current.style.color = 'black'
                }
                this.setState({sabadoBox:event.target.checked})
                break
            case 6:
                if(this.domingo.current.checked){
                    this.timeDomingo.current.disabled = false
                    this.timeDomingo.current.value='12:00'
                    this.timeDomingo.current.style.color = 'green'
                }else {
                    this.timeDomingo.current.disabled = true
                    this.timeDomingo.current.value=''
                    this.timeDomingo.current.style.color = 'black'
                }
                this.setState({domingoBox:event.target.checked})
                break
            default:
                break
        }
    }

    handleChange(event) {
        /**Preapara para subir a firebase */
        if (event.target.files[0]) {
            const image2 = event.target.files[0];
            this.setState(() => ({ image2 }));
        }
        /**Muestra */
        this.imagen2.current.src = URL.createObjectURL(event.target.files[0])
    }

    async verificarNickname(){
        var checknick = this.nicknameInput.current.value
        if( checknick === this.state.data.data.nickname ) return true
        const url = ipserver+'api/user/check/nickname/'+checknick.toString()
            const response = await fetch(url,{
                method: 'get' ,
                headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
                },
            })
        const data = await response.json()
        if( data.status === 0 ) return true
        alert('Ya existe un usuario con ese Nickname')
        return false
    }

    async verificarMail(){
        var checkmail = this.emailInput.current.value
        if( checkmail === this.state.data.data.mail ) return true
        const url = ipserver+'api/user/check/mail/'+checkmail.toString()
        const response = await fetch(url,{
                method: 'get' ,
                headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
                },
        })
        const data = await response.json()
        if( data.status === 0 ) return true
        alert('Ya existe un usuario con ese E-mail')
        return false
    }

    verifcarCampos(){
        if( this.nicknameInput.current.value === ''){
            alert('Campo nickname vacio')
            return false
        }else if(  this.nicknameInput.current.value.length > 14 ){
            alert('Tu nickname no puede superar los 14 caracteres')
            return false
        }
        if( this.emailInput.current.value === ''){
            alert('Campo e-mail vacio')
            return false
        }else if( !this.emailInput.current.value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) ){
            alert('El E-mail no tiene un formato valido')
            return false
        }
        if( this.passInput.current.value === ''){
            alert('No escribiste una Contraseña')
            return false
        }
        return true
    }

    async actualizarUsuario(){

        if(this.state.data.data.nickname !== 'Invitado'){
            this.divscroll.current.scrollTo(0, 0)
            if( this.verifcarCampos() ){
                if( await this.verificarNickname() && await this.verificarMail()){      
                    this.handleUpload()
                }
            }
        }else{
            alert('No se puede editar la cuenta de Invitado!')
        }
   
    }

    async actualizarUsuarioServer(){
        var query = JSON.stringify({ 
            "id": this.state.data.data.id,
            "nickname": this.nicknameInput.current.value,
            "mail": this.emailInput.current.value,
            "pass": this.passInput.current.value,
            "foto": this.state.url,
            "L":this.lunes.current.checked,
            "M":this.martes.current.checked,
            "MI":this.miercoles.current.checked,
            "J":this.jueves.current.checked,
            "V":this.viernes.current.checked,
            "S":this.sabado.current.checked,
            "D":this.domingo.current.checked,
            "HL":this.timeLunes.current.value,
            "HM":this.timeMartes.current.value,
            "HMI":this.timeMiercoles.current.value,
            "HJ":this.timeJueves.current.value,
            "HV":this.timeViernes.current.value,
            "HS":this.timeSabado.current.value,
            "HD":this.timeDomingo.current.value    
        })
        const url = ipserver+'api/user/'
        const response = await fetch(url,{
            method: 'put' ,
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: query   
          })
      
        const res = await response.json()
        //console.log(res)
        if(  (res.data.foto === 'defecto') || (!res.data.foto)  ){
            this.imagen.current.src = fotodefecto
        }else {
            this.imagen.current.src = res.data.foto
        }
        this.setState({
            data:res,
            nick:res.data.nickname,
            url: res.data.foto,
            mensaje:"Actualizaste tu perfil correctamente"
        })
    }

    
    async actualizarPersonaje(){

        if(this.state.data.data.nickname !== 'Invitado'){
           
            var alianzaBoolean = false
            if( this.alianzaInput.current.value=== 'Si' ) alianzaBoolean = true
            var query = JSON.stringify({ 
                "id": this.state.char.id,
                "id_user": this.state.data.data.id,
                "nombre": this.nombreInput.current.value,
                "clase": this.claseInput.current.value,
                "nivel": this.nivelrange.current.value,
                "arma":this.arma.current.value,
                "armadura":this.armadura.current.value,
                "botas":this.botas.current.value,
                "guantes":this.guantes.current.value,
                "cinturon":this.cinturon.current.value,
                "rankeado":alianzaBoolean
            })
            const url = ipserver+'api/char/'
            const response = await fetch(url,{
                method: 'put' ,
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
                body: query   
              })
            const res = await response.json()
            this.divscroll.current.scrollTo(0, 0)
            this.setState({
                mensaje:res.message
            })

        }else{
            alert('No se puede editar la cuenta de Invitado!')
        }


        
    }

    handleUpload(){
        if( !this.state.image2 ){
            this.actualizarUsuarioServer()    
        }else{
            const { image2 } = this.state;
            const uploadTask = storage.ref(`images/${image2.name}`).put(image2);
            uploadTask.on(
            "state_changed",
            snapshot => {
                // progress function ...
                const progress = Math.round(
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                );
                this.setState({ 
                    mensaje:'Subiendo tu imagen: '+progress+' %'
                });
            },
            error => {
                // Error function ...
                console.log(error);
            },
            () => {
                // complete function ...
                storage
                .ref("images")
                .child(image2.name)
                .getDownloadURL()
                .then(url => {
                    /**Manda a actualizar subiendo la imagen */
                    this.setState({url})
                    this.actualizarUsuarioServer()
                });     
            }
            );
        }        
    }

    render(){

        return(

               <div >
                   <Header/>
                    <div className='editar-div-1' > 

                        {/* Barra de navegacion  */}
                        <nav className='editar-nav-1'>     
                            
                            <label title="Pagina principal" className="nav-link editar-texto-1" onClick={this.nextPageInicio.bind(this)} >Inicio</label>
                            <label title="Lista de Miembros"  className="nav-link editar-texto-1" onClick={this.nextPageMiembros.bind(this)} >Miembros</label>
                            <label title="Graficos"  className="nav-link editar-texto-1" onClick={this.nextPageEstadisticas.bind(this)}>Estadisticas</label>
                            <label title="Guias PvE PvP"  className="nav-link editar-texto-1" onClick={this.nextPageGuias.bind(this)} >Guias</label>
                            <label title="Editar Perfil"  className="nav-link editar-texto-1" >Editar Perfil</label>             
                           
                        </nav>   
                        
                        {/* Perfil */}
                        <div className='editar-div-perfil'> 
                            <center >                                                     
                                <img className='editar-image-1'  ref={this.imagen}  alt='150x150'/>                                                                          
                                <center>
                                    <p className='editar-texto-2'>{this.state.nick}</p> 
                                </center>                                                                                                          
                                <button className="btn4 btn-danger fa fa-sign-out" onClick={this.logout.bind(this)} title="Logout">logout</button>  
                            </center> 
                        </div>   

                        {/**Formulario */}
                        <div className='editar-div-2' ref={this.divscroll}>   
                          
                            <form className='editar-div-3'>
                                
                                <h2 className="editar-texto-3">Editar Perfil</h2> 
                                <center>
                                    <p className='editar-texto-5' >{this.state.mensaje}</p> 
                                    <img className="editar-image-2" ref={this.imagen2} alt='150x150'/>
                                </center><br/>
                                <label htmlFor="files" className="btn btn-success">Selecciona Imagen</label>
                                <input className='editar-boton-file' id="files" type="file" aria-describedby="inputGroupFileAddon01" onChange={this.handleChange.bind(this)} /><br/>
                                <label className='editar-texto-4'>Nickname</label> 
                                <input type="email" ref={ this.nicknameInput } className="form-control mb-4" placeholder="Nickname" />
                                <label  className='editar-texto-4'>E-mail</label> 
                                <input type="text"  ref={ this.emailInput } className="form-control mb-4" placeholder="E-mail"/>
                                <label  className='editar-texto-4'>Password</label> 
                                <input type="password" ref={ this.passInput } className="form-control mb-4" placeholder="Nueva pass"/><br/><br/>
                                <h2 className="App-texto-9">Selecciona tus horarios</h2> <br/>
                                <div className='editar-div-4'>
                                    <div  className='editar-div-5'>
                                        <p className='editar-texto-4'>Lunes</p>
                                        <input ref={this.lunes} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,0)}}/><br/>
                                        <input ref={this.timeLunes}  type='time'/>
                                    </div>    
                                    <div  className='editar-div-5'>
                                        <p className='editar-texto-4'>Martes</p>
                                        <input ref={this.martes} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,1)}}/><br/>
                                        <input ref={this.timeMartes} type='time'/>
                                    </div>  
                                    <div className='editar-div-5'>
                                        <p className='editar-texto-4'>Miercoles</p>
                                        <input ref={this.miercoles} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,2)}}/><br/>
                                        <input ref={this.timeMiercoles} type='time'/>
                                    </div>  
                                    <div className='editar-div-5'>
                                        <p className='editar-texto-4'>Jueves</p>
                                        <input ref={this.jueves} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,3)}}/><br/>
                                        <input ref={this.timeJueves} type='time'/>
                                    </div>  
                                    <div className='editar-div-5'>
                                        <p className='editar-texto-4'>Viernes</p>
                                        <input ref={this.viernes} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,4)}}/><br/>
                                        <input ref={this.timeViernes} type='time'/>
                                    </div>  
                                    <div className='editar-div-5'>
                                        <p className='editar-texto-4'>Sabado</p>
                                        <input ref={this.sabado} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,5)}}/><br/>
                                        <input ref={this.timeSabado} type='time'/>
                                    </div>  
                                    <div className='editar-div-5'>
                                        <p className='editar-texto-4'>Domingo</p>
                                        <input ref={this.domingo} type='checkbox' onChange={(e)=>{this.checkboxOnChange(e,6)}}/><br/>
                                        <input ref={this.timeDomingo} type='time'/>
                                    </div>                   
                                </div><br/><br/>
                                <button type="button" className="btn btn-info" onClick={this.actualizarUsuario.bind(this)}>Actualizar Usuario</button><br/><br/>
                                <h2 className="editar-texto-3">Datos de tu personaje</h2> <br/>
                                <label  className='editar-texto-4'>Nombre</label> 
                                <input ref={this.nombreInput} name="text" type="text" className="form-control mb-4" placeholder="Nombre"/>
                                <label  className='editar-texto-4'>Clase</label> 
                                <select id="select" ref={this.claseInput} name="select" className="custom-select fa">
                                    <option >Sorccer</option>
                                    <option >Archer</option>
                                    <option >Gunner</option>
                                    <option >Reaper</option>
                                    <option >Slayer</option>
                                    <option >Warrior</option>
                                    <option >Lancer</option>
                                    <option >Priest</option>
                                    <option >Mystic</option>
                                    <option >Berserker</option>
                                </select>    
                               
                                <label className='editar-texto-4'>Nivel: { this.state.slidebarValue }</label>         
                                <input type="range" className="custom-select" ref = { this.nivelrange } name="rangeInput" min="1" max="65" onChange={() => this.rangeNivel()}/>        
                                
                               
                                <label  className='editar-texto-4'>Arma</label> 
                                <select ref={this.arma} className="custom-select" >           
                                    <option >Deviate</option>
                                    <option >Fulminate</option>
                                    <option >Wroth</option>
                                    <option >Schima</option>
                                    <option >Tensus</option>
                                    <option >Starfall</option>
                                    <option >Lucid</option>
                                    <option >Otro</option>
                                </select>
                           
                               
                                <label  className='editar-texto-4'>Armadura</label>        
                                <select ref={this.armadura} className="custom-select" >           
                                    <option >Deviate</option>
                                    <option >Fulminate</option>
                                    <option >Wroth</option>
                                    <option >Schima</option>
                                    <option >Tensus</option>
                                    <option >Starfall</option>
                                    <option >Lucid</option>
                                    <option >Otro</option>
                                </select>
                                   
                                
                                <label  className='editar-texto-4'>Guantes</label>  
                                <select ref={this.guantes} className="custom-select" >           
                                            <option >Deviate</option>
                                            <option >Fulminate</option>
                                            <option >Wroth</option>
                                            <option >Schima</option>
                                            <option >Tensus</option>
                                            <option >Starfall</option>
                                            <option >Lucid</option>
                                            <option >Otro</option>
                                </select>
                                   
                              
                                <label  className='editar-texto-4'>Botas</label> 
                                <select ref={this.botas} className="custom-select" >           
                                            <option >Deviate</option>
                                            <option >Fulminate</option>
                                            <option >Wroth</option>
                                            <option >Schima</option>
                                            <option >Tensus</option>
                                            <option >Starfall</option>
                                            <option >Lucid</option>
                                            <option >Otro</option>
                                </select>
                               
                               
                                <label  className='editar-texto-4'>Cinturon</label> 
                                <select ref={this.cinturon} className="custom-select" >           
                                            <option >Deviate</option>
                                            <option >Fulminate</option>
                                            <option >Wroth</option>
                                            <option >Schima</option>
                                            <option >Tensus</option>
                                            <option >Starfall</option>
                                            <option >Lucid</option>
                                            <option >Otro</option>
                                </select>
                                   
                                                   
                                <label  className='editar-texto-4'>Rankeado Alianza</label>            
                                <select ref={this.alianzaInput} className="custom-select">
                                    <option >Si</option>
                                    <option >No</option>
                                </select> <br/><br/>
                                
                                <button type="button" className="btn btn-info" onClick={this.actualizarPersonaje.bind(this)}>Actualizar Personaje</button>
                                
                            </form>
                        </div>
                    </div> 
               </div>
                        
        )
    }
}

export default withRouter(EditarperfilPage)